CA,UA5DN,CACF,CC5BI,0BD6FA,oCAEE,mBAAA,CADA,gBA7DJ,CA+DI,sDACE,WAAA,CACA,UA7DN,CAiEM,kEAEE,aEpIG,CFmIH,cA9DR,CAiEM,wEAEE,aEvIG,CFsIH,cA9DR,CACF","sourcesContent":["\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n@import 'resources/scss/user/variables';\n@import 'resources/scss/user/colors';\n\nhr {\n  background: #D3D3D3;\n  opacity: 1;\n}\n\n#hr-section-menu {\n  display: none;\n\n  @include respond-to(md) {\n    display: block;\n  }\n}\n\n#hr-section-menu-mobile {\n  display: block;\n\n  @include respond-to(md) {\n    display: none;\n  }\n}\n\n#menu {\n  min-width: 100%;\n  z-index: 999;\n  top: 0;\n  left: 0;\n  position: fixed;\n\n  nav#top-menu {\n    display: none;\n    background-color: #ffffff!important;\n    .brand-info {\n      span, a {\n        text-align: right;\n        display: block;\n        text-decoration: none;\n      }\n\n      .brand-info-title {\n        font-size: 18px;\n        color: $color-dark;\n      }\n      .brand-info-description {\n        font-size: 12px;\n        color: $color-gray;\n      }\n    }\n  }\n\n\n  @include respond-to(md) {\n    nav#top-menu {\n      display: block;\n      padding-top: 54px;\n      padding-bottom: 24px;\n\n      .navbar-brand img {\n        height: 38px;\n        width: auto;\n      }\n    }\n  }\n\n  @include respond-to(xl) {\n    nav#top-menu {\n      padding-top: 62px;\n      padding-bottom: 27px;\n      .navbar-brand img {\n        height: 56px;\n        width: auto;\n      }\n\n      .brand-info {\n        .brand-info-title {\n          font-size: 24px;\n          color: $color-dark;\n        }\n        .brand-info-description {\n          font-size: 16px;\n          color: $color-gray;\n        }\n      }\n    }\n\n\n  }\n}\n\n","@import 'colors';\n\n$container-max-widths: (\n        sm: 100%,\n        md: 740px,\n        lg: 1080px,\n        xl: 1280px,\n);\n$grid-breakpoints: (\n        xs: 0,\n        sm: 576px,\n        md: 767px,\n        lg: 1200px,\n        xl: 1365px,\n);\n\n$font-family-base: 'Rubik', sans-serif;\n$breadcrumb-item-padding-x: 0.1rem;\n$breadcrumb-active-color: $color-orange;\n$breadcrumb-divider-color: $color-gray;\n\n/// Mixin to manage responsive breakpoints\n/// @author Fulliton\n/// @param {String} $breakpoint - Breakpoint name\n/// @require $breakpoints\n@mixin respond-to($breakpoint) {\n  // If the key exists in the map\n  @if map-has-key($grid-breakpoints, $breakpoint) {\n    // Prints a media query based on the value\n    @media (min-width: map-get($grid-breakpoints, $breakpoint)) {\n      @content;\n    }\n  }\n\n    // If the key doesn't exist in the map\n  @else {\n    @warn \"Unfortunately, no value could be retrieved from `#{$breakpoint}`. \"\n        + \"Available breakpoints are: #{map-keys($grid-breakpoints)}.\";\n  }\n}\n\n@mixin respond-up($breakpoint) {\n  // If the key exists in the map\n  @if map-has-key($grid-breakpoints, $breakpoint) {\n    // Prints a media query based on the value\n    @media (max-width: map-get($grid-breakpoints, $breakpoint)) {\n      @content;\n    }\n  }\n\n    // If the key doesn't exist in the map\n  @else {\n    @warn \"Unfortunately, no value could be retrieved from `#{$breakpoint}`. \"\n        + \"Available breakpoints are: #{map-keys($grid-breakpoints)}.\";\n  }\n}","$color-orange: #F4B12F;\n$color-dark: #1A1819;\n$color-gray: #767676;\n$color-light-gray: #F1F1F1;"],"sourceRoot":""}]);
